.about-main{
  min-height: $vh;
  display: flex;
  align-items: flex-end;
  padding: 10em 0;
  position: relative;
  .container{
    width: 100%;
    position: relative;
    z-index: 2;
  }
  &__inner{
    width: 37em;
    max-width: 100%;
    margin-left: auto;
  }
  &__text{
    text-align: right;
  }
}

.m-video{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.about{
  overflow: hidden;
  background-image: url(../img/logo-bg.svg);
  background-repeat: no-repeat;
  background-position: right 3.75em top 5em;
  background-size: 20.125em auto;
  position: relative;
  z-index: 2;
  &__inner{
    margin-top: -13em;
    max-width: 100em;
  }
  &__desc{
    margin: .5em 0 0 auto;
    width: 58em;
    max-width: 100%;
    text-align: right;
  }
}

.about-counts{
  margin-top: 6em;
  display: grid;
  grid-template-columns: 2fr 1.5fr 1fr;
  gap: 3em;
  padding: 0 6.25em;
}

.count-item{
  text-align: center;
  &__top{
    font-weight: 700;
    font-size: 13.75em;
    line-height: 95%;
    text-transform: uppercase;
  }
  &__years{
    font-weight: 700;
    font-size: 6.25em;
    line-height: 95%;
    color: $primary;
    margin: -.3em 0 .2em;
  }
  &__subtitle{
    font-weight: 700;
    font-size: 3.4375em;
    line-height: 95%;
    color: $primary;
    margin: -.2em 0 .27em;
  }
}

.about-news{
  margin-top: 1em;
  width: 67.5em;
  max-width: 100%;
  display: block;
  position: relative;
  &:not(:hover){
    .about-news__img{
      opacity: .8;
    }
  }
  &__img{
    transition: all .3s;
    img{
      width: 100%;
      display: block;
    }
  }
}

.m-badge{
  position: absolute;
  left: 0;
  bottom: 0;
  padding: 2.0625em 2.8125em;
  background-color: $black;
  text-transform: uppercase;
  font-weight: 300;
  span{
    font-size: 1.625em;
    line-height: 130%;
  }
}


@media screen and (max-width: 992px){
  .about-main{
    padding: 5em 0;
    &__inner{
      width: 25em;
    }
  }

  .about{
    background-image: unset;
    padding: 2em 0;
    &__inner{
      margin-top: 0;
    }
    &__desc{
      text-align: left;
      br{
        display: none;
      }
    }
  }

  .about-counts{
    margin-top: 2em;
    gap: 1.25em;
    padding: 0;
  }

  .count-item{
    font-size: .625em;
    &__top{
      font-size: 9em;
    }
    &__years{
      font-size: 4.5em;
    }
    &__subtitle{
      font-size: 2.5em;
    }
  }

  .about-news{
    margin-top: 2em;
  }

  .m-badge{
    padding: .5em 1em;
    span{
      font-size: 1.125em;
    }
  }
}


@media screen and (max-width: 576px){
  .about-counts{
    grid-template-columns: 100%;
  }
}
