@import 'vars';
html {
    width: 100%;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
}

.m-page {
    width: calc(100vw - (100vw - 100%));
    overflow-x: hidden;
    font-family: $font1;
    font-weight: 400;
    color: $white;
    background-color: $black;
    font-size: calc(100vw * (16 / 1920));
    //font-size: calc(1440px * (16 / 1440));
    //@media screen and (max-width: 1440px){
    //    font-size: calc(100vw * (16 / 1440));
    //}
    &.fixed-body,
    &.fixed-body2{
        overflow-y: hidden;
        padding-right: var(--scrollWidth);
    }
    &--white {
        color: $black;
        background-color: $white;
    }
}

.header._open,
.m-page--white{
    .header__logo{
        path{
            fill: $black;
        }
    }
    .burger{
        &:before,
        &:after{
            background-color: $black;
        }
    }
}

.container {
    width: 100%;
    margin: 0 auto;
    padding: 0 5em;
}

a {
    &:hover {
        color: $primary;
    }
}


@media screen and (max-width: 992px){
    .m-page{
        font-size: calc(100vw * (16 / 768));
    }

    .container{
        padding: 0 1em;
    }
}


@media screen and (max-width: 576px){
    .m-page{
        font-size: calc(100vw * (16 / 360));
    }
}


