.catalog{
  padding-top: 13em;
  &__wrap{
    position: relative;
  }
  &__panel{
    position: absolute;
    top: 0;
    left: 5em;
    height: 100%;
    padding-top: 1.625em;
    img{
      position: sticky;
      top: 1.625em;
      width: 2.75em;
      display: block;
    }
  }
}

.catalog-group{
  overflow: hidden;
  padding-bottom: 11.5em;
  min-height: 93.75em;
  &__inner{
    padding-left: 6em;
    position: relative;
  }
  &__arrow{
    position: absolute;
    width: 4.6875em;
    bottom: 8.25em;
    right: 0;
    svg{
      width: 100%;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
  &__title{
    font-size: 5.3125em;
    line-height: 95%;
    font-weight: 600;
    opacity: .95;
  }
  &__text{
    margin-top: 6em;
    text-align: center;
    .text30{
      line-height: 120%;
      letter-spacing: -0.02em;
      font-weight: 600;
    }
  }
  &__nav{
    margin-top: 4em;
    @include center;
  }
  &__btn{
    display: flex;
    align-items: center;
    font-family: $font2;
    font-weight: 700;
    span{
      letter-spacing: 0.02em;
      font-size: 1.25em;
      text-transform: uppercase;
    }
    svg{
      width: 3.5em;
      margin-left: .75em;
    }
    &:hover{
      rect{
        fill: $primary;
      }
    }
  }
  &__btn2{
    margin-left: 1.875em;
  }
}

.catalog-card{
  display: block;
}

.catalog-slider{
  margin-top: 3.5em;
  .swiper{
    &-container{
      width: 53.5em;
      margin: 0 auto;
      max-width: 100%;
    }
    &-wrapper{
      align-items: center;
    }
    &-slide{
      @include center;
      transition: all .3s;
      opacity: 0;
      &-prev,
      &-next{
        opacity: .5;
        .catalog-card{
          width: 30em;
        }
      }
      &-active{
        opacity: 1;
      }
      &-prev{
        justify-content: flex-end;
      }
      &-next{
        justify-content: flex-start;
      }
    }
  }
}

.catalog-card{
  width: 100%;
  img{
    max-width: 100%;
    max-height: 50.5em;
    display: block;
  }
}


@media screen and (max-width: 992px){
  .catalog{
    padding-top: 5em;
    &__panel{
      display: none;
    }
  }

  .catalog-group{
    min-height: unset;
    padding-bottom: 2em;
    &__inner{
      padding-left: 0;
    }
    &__title{
      font-size: 2em;
    }
    &__text{
      margin-top: .25em;
    }
    &__nav{
      margin-top: 1.5em;
    }
    &__arrow{
      display: none;
    }
  }

  .catalog-slider{
    margin-top: 1.5em;
    .swiper{
      &-container{
        width: 22em;
      }
      &-slide{
        &-next,
        &-prev{
          .catalog-card{
            width: 17em;
          }
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-group{
    &__nav{
      flex-direction: column;
    }
    &__btn{
      font-size: .75em;
    }
    &__btn2{
      margin: 1em 0 0;
    }
  }
}
