.engineering-info{
  background-image: url(../img/bg.svg);
  background-size: cover;
  padding: 7em 0;
  &__inner,
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.5em));
    gap: 6.75em 3em;
  }
  &__item{
    display: flex;
    align-items: flex-start;
    &:before{
      content: '';
      width: 4.6875em;
      height: 4.6875em;
      background-image: url(../img/arrow.svg);
      @include bgImgSize(100%);
      margin-right: 5em;
      flex-shrink: 0;
    }
    &-text{
      font-size: 1.875em;
      line-height: 130%;
    }
  }
  &__inner{
    @media screen and (min-width: 993px){
      .engineering-info__items{
        grid-template-columns: 100%;
        gap: 5em;
        min-height: 100%;
      }
    }
  }
}

.engineering-card2,
.engineering-card,
.news-card{
  position: relative;
  display: block;
  &:hover{
    color: $white;
  }
  @media screen and (min-width: 993px){
    overflow: hidden;
    &:hover{
      .engineering-card__img,
      .engineering-card2__img{
        transform: scale(1.05);
      }
    }
  }
  &__img{
    transition: all .6s;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;
  }
  &__inner{
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
  }
}

.engineering-card{
  &__inner{
    padding: 7em 0 4.375em;
    min-height: 95em;
  }
  &__subtitle{
    font-size: 3.125em;
    line-height: 95%;
    color: $primary;
    margin-bottom: .4em;
    font-weight: 700;
    text-transform: uppercase;
  }
  &__bottom{
    padding-top: 3em;
    margin-top: auto;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    &-col{
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
  }
  &__desc{
    max-width: 37em;
  }
  &__arrow{
    width: 4.6875em;
    margin-left: 1em;
    flex-shrink: 0;
    @include center;
    img{
      width: 100%;
    }
  }
  &__link{
    margin-top: 17.5em;
  }
  &__top{
    margin-bottom: auto;
    padding-bottom: 1.5em;
    max-width: 50em;
  }
  &._type2{
    .engineering-card__inner{
      justify-content: flex-end;
    }
    .engineering-card__bottom{
      margin: 0;
    }
    .engineering-card__desc{
      max-width: 93.25em;
    }
    @media screen and (min-width: 993px){
      .engineering-card__link{
        margin-top: 4em;
      }
    }
  }
  &._type3{
    @media screen and (min-width: 993px){
      .engineering-card__inner{
        justify-content: flex-end;
      }
      .h1{
        font-size: 6em;
      }
      .engineering-card__link{
        margin-top: 2.5em;
      }
      .engineering-card__desc{
        max-width: 42.625em;
      }
      .engineering-card__bottom{
        margin-top: 0;
        padding-top: 2em;
      }
    }
  }
}

.engineering-card2{
  &__inner{
    padding: 11em 0;
    justify-content: space-between;
  }
  &__top{
    display: grid;
    grid-template-columns: 48.5em 45.5em;
    justify-content: space-between;
    gap: 1em;
  }
  &__text2{
    text-align: right;
  }
  &__title{
    padding: 2em 0;
  }
  &__prop-num,
  &__title{
    font-size: 6em;
    line-height: 95%;
    font-weight: 700;
    text-transform: uppercase;
    max-width: 17em;
  }
  &__bottom{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
  &__props{
    display: flex;
    align-items: flex-start;
  }
  &__prop{
    @include grid100gap(1.25em);
    margin-right: 7em;
    &:last-child{
      margin: 0;
    }
    &-num{
      text-transform: unset;
      color: $gray;
    }
  }
  &__arrow{
    width: 6.6875em;
    img{
      width: 100%;
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .engineering-info{
    padding: 2em 0;
    background-size: 140% auto;
    &__inner,
    &__items{
      grid-template-columns: 100%;
      gap: 2em;
    }
    &__item{
      &:before{
        width: 3em;
        height: 3em;
        margin-right: 1em;
      }
      &-text{
        font-size: 1.25em;
      }
    }
  }

  .engineering-card{
    &__inner{
      min-height: unset;
      padding: 2.5em 0;
    }
    &__subtitle{
      font-size: 1.5em;
    }
    &__bottom{
      padding-top: 1.25em;
    }
    &__arrow{
      width: 2em;
    }
    &__link{
      margin-top: 1.5em;
    }
    &._type2{
      padding-top: 5em;
    }
  }

  .engineering-card2{
    &__inner{
      padding: 2.5em 0;
    }
    &__top{
      grid-template-columns: 100%;
      max-width: 30em;
      br{
        display: none;
      }
    }
    &__text2{
      text-align: left;
    }
    &__prop-num,
    &__title{
      font-size: 2.25em;
    }
    &__prop{
      margin-right: 3em;
      gap: .75em;
    }
    &__arrow{
      width: 3em;
    }
  }
}


@media screen and (max-width: 576px){
  .engineering-info{
    &__item{
      &:before{
        width: 2em;
        height: 2em;
      }
      &-text{
        font-size: 1em;
      }
    }
  }

  .engineering-card{
    &__subtitle{
      font-size: 1.25em;
    }
    &__bottom{
      display: block;
    }
    &__arrow{
      display: none;
    }
  }

  .engineering-card2{
    &__props{
      justify-content: space-between;
      width: 100%;
    }
    &__prop{
      margin-right: 1em;
    }
    &__arrow{
      display: none;
    }
    &__prop-num,
    &__title{
      font-size: 1.875em;
    }
  }
}
