.news{
  padding-top: 11.875em;
}

.news-card{
  &:not(:hover){
    @media screen and (min-width: 993px){
      color: rgba(255, 255, 255, 0.5);
      .h1 .primary-text{
        color: rgba(255, 255, 255, 0.5);
      }
      .primary-text{
        color: $gray;
      }
    }
  }
  &__inner{
    min-height: 70.75em;
    justify-content: flex-end;
    padding: 2.5em 0 7.75em;
  }
  &__date{
    font-weight: 700;
    font-size: 4.375em;
    line-height: 95%;
    text-transform: uppercase;
    margin-bottom: auto;
    margin-left: auto;
    transition: all .3s;
  }
  .h1{
    transition: all .3s;
    span{
      //transition: all .3s;
    }
  }
  &__desc{
    padding: 2em 0 2.875em;
    max-width: 40em;
    width: 100%;
    p{
      line-height: inherit;
      margin-bottom: 1em;
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
  &._type2{
    .news-card__desc{
      max-width: 48em;
    }
  }
}

.article{
  padding: 3.5em 0 6em;
  background-image: url(../img/article-bg.svg);
  background-size: cover;
  &__top{
    position: relative;
  }
  &__date{
    position: absolute;
    right: 0;
    top: .2em;
    font-size: 4.375em;
    line-height: 95%;
    font-weight: 700;
    color: $primary;
  }
  &__content{
    margin-top: 1.5em;
    .about-news{
      float: left;
      width: 67.5em;
      max-width: 100%;
      margin: 0 3.5em 3.75em 0;
      &__img{
        opacity: 1 !important;
        img{
          width: 100%;
          margin: 0;
        }
      }
    }
    .about-news2{
      width: 45.25em;
    }
  }
}

.text{
  p{
    font-size: 1.875em;
    line-height: 130%;
    margin-bottom: 1.75em;
    &:last-child{
      margin-bottom: 0;
    }
  }
  img{
    width: 70.625em;
    max-width: 100%;
    float: right;
    margin: 0 0 3.75em 3.75em;
  }
}


@media screen and (max-width: 992px){
  .news{
    padding-top: 4em;
  }

  .news-card{
    margin-top: 3em;
    font-size: .75em;
    &__date{
      position: absolute;
      bottom: 100%;
      left: 0;
      margin-bottom: .25em;
    }
    &__inner{
      padding: 3.5em 0;
      justify-content: flex-end;
      min-height: 23em;
    }
    &__date{
      margin-left: 0;
      font-size: 1.25em;
    }
    &__desc{
      padding: 1em 0 0;
      font-size: .875em;
      order: 2;
    }
  }

  .article{
    padding: 2em 0;
    &__date{
      position: static;
      font-size: 1.5em;
      margin-top: .5em;
    }
    &__content{
      margin-top: 1em;
    }
    .about-news{
      width: 100%;
      margin: 0 0 1.5em;
    }
  }

  .text{
    p{
      margin-bottom: 1em;
      font-size: 1.25em;
    }
    img{
      margin: 0 0 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .article{
    &__date{
      font-size: 1.125em;
    }
    .about-news{
      margin: 0 0 1em;
    }
  }

  .text{
    p{
      font-size: 1em;
    }
    img{
      margin: 0 0 1em;
    }
  }
}
