.main{
  position: relative;
  height: $vh;
  overflow: hidden;
  display: flex;
  align-items: center;
  &__link{
    font-weight: 900;
    font-size: 6.5em;
    letter-spacing: -0.02em;
    transition: all .3s;
  }
  &__btn{
    position: absolute;
    right: 4.75em;
    top: 50%;
    transform: translateY(-50%);
    z-index: 3;
  }
}

.main-slider{
  height: 7.75em;
  width: 100%;
  .swiper{
    &-container{
      height: 100%;
    }
    &-slide{
      @media screen and (min-width: 993px){
        padding: .625em 5em .625em 27em;
        display: flex;
        opacity: .2;
        transition: all .3s;
        &:not(.swiper-slide-active){
          .main__link{
            -webkit-text-fill-color: transparent;
            -webkit-text-stroke: 1px;
          }
        }
        &-prev,
        &-next{
          opacity: 0.4;
        }
        &-active{
          opacity: 1;
        }
      }
    }
  }
}

.main-info{
  width: 20em;
  position: absolute;
  left: 5em;
  top: 50%;
  transform: translateY(-50%);
  z-index: 3;
  &__top{
    padding: .625em 0;
  }
  &__title{
    font-weight: 900;
    font-size: 6.5em;
    letter-spacing: -0.02em;
    text-transform: uppercase;
  }
  &__bottom{
    position: absolute;
    top: 100%;
    left: 0;
    margin-top: .875em;
    width: 100%;
    height: calc((50 * var(--vh)) - 4.375em - .875em);
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    padding-bottom: 7em;
  }
  &__text{
    font-size: 1.375em;
    line-height: 120%;
  }
}



@media screen and (max-width: 992px){
  .main{
    padding: 5em 1em 2em;
    display: block;
    overflow-y: auto;
    &__link{
      font-size: 2.25em;
    }
    &__btn{
      display: none;
    }
  }

  .main-info{
    position: relative;
    padding: 2em 0 1em;
    height: unset;
    left: unset;
    top: unset;
    transform: unset;
    &__top,
    &__text{
      display: none;
    }
    &__bottom{
      padding: 0;
      position: static;
      height: unset;
    }
  }

  .main-slider{
    height: auto;
    .swiper{
      &-wrapper{
        display: block !important;
        transform: unset !important;
      }
      &-slide{
        padding-bottom: 1em;
        &-duplicate{
          display: none;
        }
      }
    }
  }
}


