.about-done{
  display: grid;
  grid-template-columns: calc(100% - 50em) 38.5em;
  gap: 1.5em 11.5em;
  padding: 0 2.25em 0 12.5em;
  margin-top: 7.25em;
  &__col{
    text-align: center;
    padding-top: 1.875em;
  }
  &__num{
    font-weight: 700;
    font-size: 18.75em;
    line-height: 95%;
    text-transform: uppercase;
    color: rgba($white, 0.87);
  }
  &__desc{
    width: 38em;
    max-width: 100%;
    margin: .875em auto 0;
  }
}

.about-duration{
  display: grid;
  grid-template-columns: 27em 67.5em;
  justify-content: space-between;
  gap: 2em 1em;
  padding: 0 2.25em 0 3em;
  margin-top: 8.75em;
  .about-news{
    &:not(:hover){
      .about-news__img{
        opacity: .4;
      }
    }
    .m-badge{
      left: unset;
      right: 0;
    }
  }
  &__col{
    text-align: center;
    padding-top: 1.5em;
  }
  &__num{
    font-weight: 700;
    font-size: 13.75em;
    line-height: 95%;
    text-transform: uppercase;
  }
  &__day{
    font-size: 5em;
    line-height: 95%;
    font-weight: 700;
    color: $primary;
    margin-top: -.2em;
  }
  &__desc{
    margin-top: .25em;
  }
  .about-news{
    margin: 0;
  }
}

.own-production{
  margin: 6.375em auto 0;
  padding: 0 0 6em 5.5em;
  width: calc(100% - 4.5em);
  border-bottom: 1px solid $white;
  position: relative;
  &__num{
    display: flex;
    align-items: center;
    font-weight: 700;
    font-size: 16.25em;
    line-height: 95%;
    text-transform: uppercase;
    color: rgba($white, 0.87);
    .animated{
      transition: all 1.6s;
      display: inline-block;
      transform: translateX(10em);
      &--active{
        transform: translateX(0);
      }
    }
    span{
      color: #707070;
      &:nth-child(3){
        opacity: .5;
      }
      &:nth-child(4){
        opacity: .3;
      }
      &:nth-child(5){
        opacity: .2;
      }
      &:nth-child(6){
        opacity: .1;
      }
    }
  }
  &__info{
    position: absolute;
    right: 0;
    display: flex;
    align-items: center;
    top: 5.375em;
    width: 50em;
    max-width: 100%;
  }
  &__desc{
    font-size: 2em;
    line-height: 130%;
  }
  &__arrow{
    width: 4.6875em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
}

.hub{
  background-image: url(../img/hub-bg.svg);
  background-size: cover;
  background-position: center;
  &__inner{
    padding: 26.75em 0 10em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  &__title{
    font-size: 6.25em;
    line-height: 95%;
    text-transform: uppercase;
    font-weight: 700;
  }
  &__link{
    margin-top: 1.875em;
    min-width: 15em;
  }
  &__link2{
    margin-top: 10em;
  }
}


@media screen and (max-width: 992px){
  .about-duration,
  .about-done{
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 2em 1em;
    padding: 0;
    margin-top: 3em;
    &__num{
      font-size: 6.25em;
    }
    &__desc{
      width: 18em;
      margin-top: .5em;
    }
  }

  .about-duration{
    &__col{
      padding: 0;
    }
    &__day{
      font-size: 3em;
    }
    &__desc{
      width: 100%;
    }
  }

  .own-production{
    padding: 0 0 2em;
    margin-top: 4em;
    width: 100%;
    &__num{
      font-size: 7em;
      span:nth-child(2)~span{
        display: none;
      }
    }
    &__info{
      position: static;
      width: 28em;
    }
    &__desc{
      font-size: 1.25em;
    }
    &__arrow{
      width: 3em;
      margin-left: .25em;
    }
  }

  .hub{
    background-image: unset;
    &__inner{
      padding: 7em 0;
    }
    &__title{
      font-size: 3em;
    }
    &__link{
      margin-top: 1.25em;
    }
    &__link2{
      margin-top: 2em;
    }
  }
}


@media screen and (max-width: 576px){
  .about-duration,
  .about-done{
    grid-template-columns: 100%;
  }

  .own-production{
    &__desc{
      font-size: 1em;
    }
    &__arrow{
      width: 2.5em;
    }
  }

  .hub{
    &__title{
      font-size: 2em;
    }
  }
}
