.footer{
  padding: 6.5em 0 4.75em;
  background-color: $white;
  color: $black;
  &__inner{
    width: calc(100% - 11.875em);
    margin: 0 auto;
    padding-top: 5em;
    border-top: 1px solid rgba($gray,.4);
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 1.6875em;
  }
  &__links{
    display: flex;
    align-items: center;
  }
  &__link{
    display: flex;
    align-items: center;
    &:last-child{
      &:after{
        display: none;
      }
    }
    &:after{
      content: '';
      height: 1.25em;
      width: 1px;
      background-color: $black;
      opacity: .2;
      margin: 0 1em;
    }
    span{
      font-size: 1.125em;
      line-height: 178%;
      letter-spacing: -0.02em;
    }
  }
  &__c{
    font-size: 1.125em;
    line-height: 178%;
    letter-spacing: -0.02em;
    font-weight: 600;
  }
}

.consult{
  display: flex;
  flex-direction: column;
  align-items: center;
  &__title{
    font-weight: 700;
    font-size: 6em;
    line-height: 110%;
    text-transform: uppercase;
    text-align: center;
    color: #1e1515;
  }
}

.consult-form{
  width: 31.25em;
  max-width: 100%;
  margin-top: 2em;
  display: flex;
  .fg{
    flex-grow: 1;
  }
  &__btn{
    margin-left: .75em;
    width: 4em;
    height: 4em;
    @include center;
    border-radius: 50%;
    background-color: $black;
    &:hover{
      background-color: $primary;
    }
    img{
      width: 2.3125em;
    }
  }
}

.footer-nav{
  margin-top: 5em;
  display: flex;
  justify-content: space-between;
}

.footer-group{
  &__title{
    font-weight: 700;
    font-size: 1.5em;
    line-height: 110%;
    text-transform: uppercase;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 1em;
  }
  &__link{
    font-weight: 600;
    font-size: 1.125em;
    line-height: 178%;
    letter-spacing: -0.02em;
    margin-bottom: .67em;
    &:last-child{
      margin-bottom: 0;
    }
    &._disabled{
      opacity: .7;
      pointer-events: none;
    }
  }
}

.footer-info{
  margin-top: 2.8125em;
  display: flex;
  justify-content: space-between;
  &__logo{
    width: 11.5em;
    img{
      width: 100px;
      display: block;
    }
  }
  &__contacts{
    padding-top: 1.5em;
    max-width: 30.11em;
    text-align: right;
    font-size: 1.125em;
    line-height: 120%;
    color: rgba(0, 0, 0, 0.3);
    a{
      white-space: nowrap;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 2em 0;
    &__inner{
      width: 100%;
      padding-top: 0;
      border: none;
    }
    &__bottom{
      display: block;
    }
    &__c{
      margin-top: 1em;
    }
  }

  .consult{
    &__title{
      font-size: 2.25em;
    }
  }

  .footer-nav{
    margin-top: 2em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1em));
    gap: 2em;
  }

  .footer-info{
    margin-top: 2em;
    display: block;
    &__contacts{
      text-align: left;
      padding-top: 1em;
      font-size: 1em;
      a{
        display: block;
        margin-top: .5em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    font-size: .7em;
    &__links{
      flex-direction: column;
      align-items: flex-start;
    }
    &__link{
      margin-bottom: .5em;
      &:after{
        display: none;
      }
      span{
        line-height: 110%;
      }
    }
    &__c{
      line-height: 110%;
    }
  }

  .consult{
    &__title{
      font-size: 1.75em;
    }
  }

  .consult-form{
    font-size: .75em;
    margin-top: 1em;
    width: 100%;
  }

  .footer-nav{
    grid-template-columns: 100%;
    gap: 1em;
  }

  .footer-group{
    font-size: .875em;
    &__link{
      line-height: 115%;
    }
  }
}
