.contacts{
  padding: 8.5em 0;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 45.75em) 43.75em;
    gap: 2em;
  }
  &__col{
    padding-top: 7.5em;
  }
  &__items{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 7.625em;
    position: relative;
    a{
      color: $primary;
    }
    .m-arrow{
      position: absolute;
      left: 0;
      top: .125em;
    }
  }
  &__item{
    font-size: 3.75em;
    &:nth-child(2){
      &:hover{
        &~.m-arrow{
          transform: translateY(7.5em);
        }
      }
    }
    &:nth-child(3){
      &:hover{
        &~.m-arrow{
          transform: translateY(11.25em);
        }
      }
    }
    &:nth-child(4){
      &:hover{
        &~.m-arrow{
          transform: translateY(15em);
        }
      }
    }
  }
  &__img{
    display: flex;
    justify-content: center;
    img{
      width: 21.5em;
      max-width: 100%;
      @media screen and (min-width: 993px){
        width: auto;
        max-width: 21.5em;
        max-height: calc(100 * var(--vh) - 17em);
      }
    }
  }
  &__bottom{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 7.5em;
  }
  &__btn{
    height: 4.125em;
    padding: 0 2.25em;
    span{
      font-size: 1.5625em;
    }
  }
  &__address{
    margin-top: 2.5em;
    max-width: 46.25em;
  }
}

.m-arrow{
  border: 1px solid $black;
  width: 5.5em;
  height: 3.5em;
  border-radius: 4.375em;
  @include center;
  transition: all .3s;
  img{
    width: 1.5em;
  }
}


@media screen and (max-width: 992px){
  .contacts{
    padding: 5em 0;
    &__inner{
      grid-template-columns: calc(100% - 24em) 22em;
    }
    &__col{
      padding-top: 1em;
    }
    &__items{
      padding-left: 0;
      .m-arrow{
        display: none;
      }
    }
    &__item{
      font-size: 1.5em;
    }
    &__bottom{
      margin-top: 2em;
    }
    &__btn{
      height: 3em;
      width: 100%;
      span{
        font-size: 1em;
      }
    }
    &__address{
      margin-top: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .contacts{
    &__inner{
      grid-template-columns: 100%;
    }
    &__img{
      display: none;
    }
  }
}
