@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.875em;
    height: 3em;
    border-radius: 3.125em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1em;
        line-height: 140%;
        letter-spacing: -0.02em;
    }
}

.m-btn-black-outline{
    border: 1px solid $black;
    color: $black;
    &:hover{
        color: $white;
        border-color: $primary;
        background-color: $primary;
    }
}

.m-btn-white-outline{
    border: 1px solid rgba($white, 0.87);
    color: $white;
    &:hover{
        color: $white;
        border-color: $primary;
        background-color: $primary;
    }
}

.m-btn-red{
    background-color: $primary;
    color: $white;
    &:hover{
        color: $white;
        background-color: darken($primary,10%);
    }
}

.m-btn-round{
    width: 7.75em;
    height: 7.75em;
    border-radius: 50%;
    @include center;
    img{
        width: 2.125em;
    }
}


@media screen and (max-width: 992px){
    .m-btn-round{
        width: 5em;
        height: 5em;
        img{
            width: 1.75em;
        }
    }
}
