.header{
  padding: 3.75em 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  object-fit: cover;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    position: relative;
    z-index: 11;
    display: block;
    flex-shrink: 0;
    width: 11.5em;
    svg{
      width: 100%;
      display: block;
    }
  }
  &._open{
    .menu{
      transform: translateX(0);
    }
    .burger{
      &:before{
        transform: rotate(-45deg) translate(-.5em,.25em);
      }
      &:after{
        transform: rotate(45deg) translate(-.5em,-.25em);
      }
    }
  }
}

.burger{
  position: relative;
  z-index: 11;
  width: 4.375em;
  height: 1.25em;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  &:after,
  &:before{
    content: '';
    width: 100%;
    height: 1px;
    background-color: $white;
    transition: all .3s;
  }
}

._flex{
  display: flex;
  align-items: center;
}

.lang{
  display: flex;
  background: #1a1a1b;
  border-radius: 18.75em;
  margin-right: 2.5em;
  color: $white;
  &__item{
    padding: 0 1.75em;
    display: flex;
    align-items: center;
    height: 3em;
    text-transform: uppercase;
    border-radius: inherit;
    span{
      font-size: .625em;
      line-height: 120%;
      letter-spacing: 0.05em;
    }
    &:hover{
      color: $white;
    }
    &._active{
      background: #d7d7d7;
      color: $black;
      pointer-events: none;
    }
  }
}

.menu{
  background-image: url(../img/menu-bg.svg);
  background-repeat: no-repeat;
  background-size: 9em auto;
  background-position: right 5.25em center;
  transform: translateX(-100%);
  transition: all .5s;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: $vh;
  padding: 17em 5em 5em;
  background-color: $white;
  color: $black;
  z-index: 10;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 7.625em;
    position: relative;
    .m-arrow{
      position: absolute;
      left: 0;
      top: .125em;
    }
  }
  &__link{
    font-size: 3.75em;
    letter-spacing: -0.03em;
    &:nth-child(2){
      &:hover{
        &~.m-arrow{
          transform: translateY(3.75em);
        }
      }
    }
    &:nth-child(3){
      &:hover{
        &~.m-arrow{
          transform: translateY(7.5em);
        }
      }
    }
    &:nth-child(4){
      &:hover{
        &~.m-arrow{
          transform: translateY(11.25em);
        }
      }
    }
    &:nth-child(5){
      &:hover{
        &~.m-arrow{
          transform: translateY(15em);
        }
      }
    }
  }
  .contacts__bottom{
    margin-top: auto;
    padding-top: 2em;
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 1em 0;
    &__logo{
      width: 7em;
    }
  }

  .burger{
    font-size: .625em;
  }

  .lang{
    margin-right: 1em;
    &__item{
      height: 2em;
      padding: 0 .5em;
    }
  }

  .menu{
    background-image: unset;
    padding: 5em 1em 1em;
    &__links{
      padding-left: 0;
      .m-arrow{
        display: none;
      }
    }
    &__link{
      font-size: 1.5em;
    }
  }
}


